.random {
    margin-bottom: 5rem;
    color: white;
    text-align: center;

    img {
        margin-top: 1rem;
        border: 5px solid white;
        border-radius: 1rem;
        width: 230px;
        
    }

}

.random-grid {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.random-control {
    margin-bottom: 0.5rem;

    button {
        font-size: 17px;
        font-weight: bold;
        margin-top: 10px;
        width: 200px;
        height: 40px;
        border: none;
        border-radius: 10px;
        background: rgb(243, 29, 90);
        cursor: pointer;
    }
}

.random-instructions {
    h4 {
        font-size: 30px;
        margin-top: 0.5rem;
        margin-bottom: 1rem;
    }

    p {
        font-size: 17px;
    }
}