.navbar{
    width: 100%;
    height: 10vh;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    border-bottom: 1px solid white;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 50;
}

.navbar-head{
    margin-left: 20px;
    ul{
        list-style: none;
    }
    a{
        color: white;
        text-decoration: none;
        font-size: 25px;
    }  

    span{
        font-size: 30px;
        color: rgb(243, 29, 90);
    }
}

.navbar-head2{
    margin-right: 20px;
    text-align: right;
    h1{
        font-size: 15px;

        span{
            color: rgb(243, 29, 90);
        }
    }
    h2{
        font-size: 10px;

        span{
            color: rgb(243, 29, 90);
        }
    }
}